}
.map-marker {
  height: 50px;
  width: 50px;
}
.leaflet-div-icon,
.map-marker {
  background-color: #fa2964;
  border: 5px solid #fa2964;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  transition: all 0.15s linear;
  z-index: 10;
}
.leaflet-div-icon:before,
.map-marker:before {
  border: 4px solid transparent;
  border-width: 10px 8px;
  border-top-color: #fa2964;
  bottom: -24px;
  height: 0;
  content: '';
  display: block;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  width: 0;
  z-index: 11;
}
.leaflet-div-icon:after,
.map-marker:after {
  background-color: #fff;
  border-radius: 50%;
  bottom: 0;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.3);
  content: '';
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 12;
}
.leaflet-div-icon i,
.map-marker i {
  color: #000;
  font-size: 23px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 13;
}
.leaflet-div-icon:hover,
.map-marker:hover {
  top: -2px;
}
.map-popup-content-wrapper {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  display: none;
  left: -100px;
  top: -210px !important;
  position: absolute;
  z-index: 9999;
}
.map-popup-content-wrapper:after {
  border: 7px solid transparent;
  border-top-color: #fff;
  bottom: -14px;
  content: '';
  display: block;
  height: 0;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  width: 0;
}
.map-popup-content-wrapper,
.leaflet-popup-content-wrapper {
  border-radius: 0;
  margin: 0;
  padding: 0;
  width: 240px;
}
.map-popup-content,
.leaflet-popup-content {
  background-color: #fff;
  height: 243px;
  margin: 0 0 8px 0;
  padding: 0;
}
.leaflet-popup-tip {
  width: 17px;
  height: 1px;
  padding: 1px;
  margin: -1px auto 0;
  transform: rotate(45deg);
}
.map-popup-content a:hover,
.leaflet-popup-content a:hover {
  text-decoration: none;
}
.map-popup-content-wrapper .close {
  color: #fff;
  display: block;
  font-size: 11px;
  opacity: 1;
  position: absolute;
  right: 15px;
  top: 15px;
  text-shadow: none;
  z-index: 99999;
}
.listing-window-image {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  height: 180px;
  position: relative;
}
.listing-window-image:after {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  content: '';
  display: block;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: opacity 0.15s linear;
  visibility: hidden;
  z-index: 100;
}
.listing-window-image:before {
  -webkit-font-smoothing: antialiased;
  background-color: #fa2964;
  border-radius: 50%;
  color: #fff;
  content: '\f002';
  display: block;
  font-family: 'FontAwesome';
  height: 32px;
  left: 50%;
  line-height: 30px;
  opacity: 0;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: opacity 0.15s linear;
  visibility: hidden;
  width: 32px;
  z-index: 101;
}
.map-popup-content-wrapper:hover .listing-window-image:before,
.map-popup-content-wrapper:hover .listing-window-image:after,
.leaflet-popup-content-wrapper:hover .listing-window-image:before,
.leaflet-popup-content-wrapper:hover .listing-window-image:after {
  opacity: 1;
  visibility: visible;
}
.listing-window-content h2,
.listing-window-content h3 {
  display: block;
  color: #323232;
}
.listing-window-content h2 {
  display: block;
  font-size: 1.1em;
  font-weight: 700;
  line-height: 1.5;
  margin: 8px 15px;
