.cluster img {
  display: none;
}
.cluster-visible {
  text-align: center;
  font-size: 15px !important;
  color: #fff !important;
  font-weight: 500;
  border-radius: 50%;
  width: 36px !important;
  height: 36px !important;
  line-height: 36px !important;
  background-color: #fa2964;
}
.cluster-visible:before {
  border: 7px solid #fa2964;
  opacity: 0.2;
  box-shadow: inset 0 0 0 4px #fa2964;
  content: '';
  position: absolute;
  border-radius: 50%;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: cluster-animation 2.5s infinite;
}
@keyframes cluster-animation {
  0%,
  100% {
    transform: scale(1.3) rotate(0deg);
  }
  50% {
    transform: scale(1.5) rotate(90deg);
  }
}
.map-marker-container {
  position: absolute;
  margin-top: 10px;
  transform: translate3d(-50%, -100%, 0);
}
.marker-container {
  position: relative;
  margin: 10px auto;
  width: 50px;
  height: 50px;
  z-index: 1;
  border-radius: 50%;
  cursor: pointer;
  top: -5px;
}
#singleListingMap .marker-container {
  cursor: default;
}
.marker-container {
  perspective: 1000;
}
.marker-card {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 0.4s ease-in-out;
  position: absolute;
  z-index: 1;
}
.marker-container:hover .marker-card,
.map-marker-container.clicked .marker-card {
  transform: rotateY(180deg) translateY(-4px);
}
.marker-arrow {
  width: 0;
  content: '';
  height: 0;
  border-style: solid;
  border-width: 8px 4px 0;
  border-color: #fa2964 transparent transparent;
  top: 49px;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  border-radius: 50%;
  z-index: 1;
}
.face {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  text-align: center;
  border-radius: 50%;
  color: #fff;
  z-index: 100;
}
.face i {
  line-height: 34px;
  font-size: 22px;
  margin-top: 0.2rem;
  margin-bottom: 0px;
}
.face.front {
  background-color: #fff;
  border: 6px solid #fa2964;
  color: #121b22;
  z-index: 100;
}
.face.back {
  transform: rotateY(180deg);
  box-sizing: border-box;
  background: #fa2964;
  border-color: #fa2964;
  color: #fff;
}
.face.back i {
  transform: scaleX(-1);
  line-height: 48px;
  font-size: 22px;
  margin-top: 0.2rem;
}
.face.back:after {
  content: '';
  display: block;
  width: 36px;
  height: 36px;
  top: 2px;
  left: 2px;
  border-radius: 50%;
  position: absolute;
}
.custom-zoom-in,
.custom-zoom-out {
  background-color: #fff;
  color: #333;
  cursor: pointer;
  border-radius: 50%;
  margin: 5px 15px;
  transition: color 0.3s, background-color 0.3s;
  box-shadow: 0 1px 4px -1px rgba(0, 0, 0, 0.2);
  text-align: center;
  font-size: 15px;
  height: 34px;
  width: 34px;
  display: none;
}
.custom-zoom-in:hover,
.custom-zoom-out:hover {
  background-color: #66676b;
  color: #fff;
}
.custom-zoom-in:before,
.custom-zoom-out:before {
  font-family: fontawesome;
  width: 100%;
  line-height: 35px;
  transition: -webkit-text-stroke 0.3s;
  -webkit-text-stroke: 1px #fff;
}
.zoomControlWrapper {
  position: absolute;
  left: 0;
  right: auto;
  width: 70px;
}
.custom-zoom-in:before {
  content: '\f067';
}
.custom-zoom-out:before {
  content: '\f068';
}
.custom-zoom-in:hover:before,
.custom-zoom-out:hover:before {
  -webkit-text-stroke: 1px #66676b;
}
